* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --white: #ffffff;
  --snow: #f8f8f8;
  --black: #0b132a;
  --yellow: #fea250;
  --bronze: #f0d4a3;
  --orange: #ff823f;
  --blue: #084d6e;
  --dark-blue: #0b132a;
  --gray: #4f5665;
  --blue-link: #3e9ec8;
  --red: #cf1919;
}

body,
input,
textarea,
button {
  font: 500 1rem Arial;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  color: var(--black);
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.3rem;
}

button {
  cursor: pointer;
}
